module.exports = "const popoverLeft = (\r\n  <Popover id=\"popover-positioned-scrolling-left\" title=\"Popover left\">\r\n    <strong>Holy guacamole!</strong> Check this info.\r\n  </Popover>\r\n);\r\n\r\nconst popoverTop = (\r\n  <Popover id=\"popover-positioned-scrolling-top\" title=\"Popover top\">\r\n    <strong>Holy guacamole!</strong> Check this info.\r\n  </Popover>\r\n);\r\n\r\nconst popoverBottom = (\r\n  <Popover id=\"popover-positioned-scrolling-bottom\" title=\"Popover bottom\">\r\n    <strong>Holy guacamole!</strong> Check this info.\r\n  </Popover>\r\n);\r\n\r\nconst popoverRight = (\r\n  <Popover id=\"popover-positioned-scrolling-right\" title=\"Popover right\">\r\n    <strong>Holy guacamole!</strong> Check this info.\r\n  </Popover>\r\n);\r\n\r\nclass Positioner extends React.Component {\r\n  render() {\r\n    return (\r\n      <ButtonToolbar style={{ padding: '100px 0' }}>\r\n        <OverlayTrigger\r\n          container={this}\r\n          trigger=\"click\"\r\n          placement=\"left\"\r\n          overlay={popoverLeft}\r\n        >\r\n          <Button>Holy guacamole!</Button>\r\n        </OverlayTrigger>\r\n        <OverlayTrigger\r\n          container={this}\r\n          trigger=\"click\"\r\n          placement=\"top\"\r\n          overlay={popoverTop}\r\n        >\r\n          <Button>Holy guacamole!</Button>\r\n        </OverlayTrigger>\r\n        <OverlayTrigger\r\n          container={this}\r\n          trigger=\"click\"\r\n          placement=\"bottom\"\r\n          overlay={popoverBottom}\r\n        >\r\n          <Button>Holy guacamole!</Button>\r\n        </OverlayTrigger>\r\n        <OverlayTrigger\r\n          container={this}\r\n          trigger=\"click\"\r\n          placement=\"right\"\r\n          overlay={popoverRight}\r\n        >\r\n          <Button>Holy guacamole!</Button>\r\n        </OverlayTrigger>\r\n      </ButtonToolbar>\r\n    );\r\n  }\r\n}\r\n\r\nrender(<Positioner />);\r\n"


