module.exports = "const popoverClick = (\r\n  <Popover id=\"popover-trigger-click\" title=\"Popover bottom\">\r\n    <strong>Holy guacamole!</strong> Check this info.\r\n  </Popover>\r\n);\r\n\r\nconst popoverHoverFocus = (\r\n  <Popover id=\"popover-trigger-hover-focus\" title=\"Popover bottom\">\r\n    <strong>Holy guacamole!</strong> Check this info.\r\n  </Popover>\r\n);\r\n\r\nconst popoverFocus = (\r\n  <Popover id=\"popover-trigger-focus\" title=\"Popover bottom\">\r\n    <strong>Holy guacamole!</strong> Check this info.\r\n  </Popover>\r\n);\r\n\r\nconst popoverClickRootClose = (\r\n  <Popover id=\"popover-trigger-click-root-close\" title=\"Popover bottom\">\r\n    <strong>Holy guacamole!</strong> Check this info.\r\n  </Popover>\r\n);\r\n\r\nrender(\r\n  <ButtonToolbar>\r\n    <OverlayTrigger trigger=\"click\" placement=\"bottom\" overlay={popoverClick}>\r\n      <Button>Click</Button>\r\n    </OverlayTrigger>\r\n    <OverlayTrigger\r\n      trigger={['hover', 'focus']}\r\n      placement=\"bottom\"\r\n      overlay={popoverHoverFocus}\r\n    >\r\n      <Button>Hover + Focus</Button>\r\n    </OverlayTrigger>\r\n    <OverlayTrigger trigger=\"focus\" placement=\"bottom\" overlay={popoverFocus}>\r\n      <Button>Focus</Button>\r\n    </OverlayTrigger>\r\n    <OverlayTrigger\r\n      trigger=\"click\"\r\n      rootClose\r\n      placement=\"bottom\"\r\n      overlay={popoverClickRootClose}\r\n    >\r\n      <Button>Click w/rootClose</Button>\r\n    </OverlayTrigger>\r\n  </ButtonToolbar>\r\n);\r\n"


